.fixedBackground{
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: var(--background_content);
}
.fixedBackground__anim{
  -webkit-animation: changeBackColor 0.1s linear;
  animation: changeBackColor 0.1s linear;
  -webkit-animation-delay: 0;
  animation-delay: 0;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
.fixedBackground__anim--long{
  -webkit-animation: changeBackColor 0.7s linear;
  animation: changeBackColor 0.7s linear;
  -webkit-animation-delay: 0;
  animation-delay: 0;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
@keyframes changeBackColor {
  0%{
    background-color: transparent;
  }
  25%{
    background-color: transparent;
  }
  50%{
    background-color: transparent;
  }
  75%{
    background-color: transparent;
  }
  99%{
    background-color: transparent;
  }
  100%{
    background-color: var(--background_content);
  }
}

.vkuiAlert{
  color: var(--main-white-color);
}
.vkuiAlert::before{
  background-color: var(--main-second-bg-color);
}
.vkuiAlert__button{
  color: var(--main-blue-color);
}
.vkuiAlert__button--destructive{
  color: var(--main-red-color);
}
.vkuiAlert__button--cancel{
  color: var(--main-blue-color);
}
